/* LANDING PAGE ============================ LANDING PAGE */

.flex-div {
  display: flex;
}

#sign-up-button {
  background-color: #566271 ;
  border: none !important;
  height: 5vh;
  font-size: 2vh;
  line-height: 5vh;
  padding: 0;
  margin: 0;
}

.text-links {
  font-family: "St Ryde light italic";
  margin-top: 2vh;
  font-size: 2vh;
  text-align: left;
  display: flex;
}

/* LOG IN ============================ LOG IN */

.login-form {
}

.form-group p {
  display: block;
}

#log-in-button,
#sign-up-link-button {
  flex-grow: 1;
  background-color: #566271 ;
  border: none !important;
  height: 5vh;
  font-size: 2vh;
  line-height: 5vh;
  padding: 0;
  font-family: "St Ryde Medium";
}

#log-in-button {
  margin-right: 2vh;
}

#sign-up-link-button {
  margin-left: 2vh;
}

.text-links {
  font-family: "St Ryde light italic";
  margin-top: 2vh;
  font-size: 2vh;
  text-align: left;
  display: flex;
}

.text-links p {
  flex: 1 1;
  color: #566271;
  margin-bottom: 2vh;
}

.text-links a p:hover {
  color: #3E4854;
}


/* UPLOAD PROJECT FORM ============================ UPLOAD PROJECT FORM */

.upload-project-form {
  background-color: #A8DCD9;
  width: 100vw;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

a:hover {
  text-decoration: none;
}

.jumbotron {
  background: transparent;
  text-align: center;
  padding: 0;
  margin-bottom: 0;
  flex-grow: 1;
}

#upload-project-button,
#sign-up-button {
  background-color: #566271 ;
  border: none !important;
  font-size: 2vh;
  line-height: 5vh;
  padding: 0;
  margin: 0;
  width: 100%;
  font-family: "St Ryde Medium";
}

.form-check-input {
  height: 100%; 
}

.status-select {
  margin-left: 1vh;
}

.date-select {
  margin-right: 1vh;
}

/* FOOTER ============================================ FOOTER */

.footer {
  background-color: #F26A21;
  font-family: "St Ryde Bold";
  color: #fff;
  font-size: calc(2vh + 1rem);
  line-height: 1.5;
  border-top: 5px solid #fff;
}

.footer a:hover,
.footer a:focus {
  opacity: 80%;
  text-decoration: none;
}

.footer-404 {
  background-color: #C6D930;
}

.footer a {
  opacity: 80%;
  color: #fff;
}

.page {
    background-color: #A8DCD9;
    width: 100vw;
    display: flex;
    flex-direction: column;
}

.vertical-center {
    margin:auto;
}

.vertical-center img {
    margin-top: 0;
}

.div-grow {
